	module.exports = {"pathContext":{"docHtml":"<h1>KAFKA Gateway &#x26; Sensor</h1>\n<p>Kafka topics act as event sources for gateway</p>\n<ol>\n<li><a href=\"#example-event-sources-definition\">Example event sources definition</a></li>\n<li><a href=\"#install-gateway\">Install gateway</a></li>\n<li><a href=\"#install-sensor\">Install sensor</a></li>\n<li><a href=\"#trigger-workflow\">Trigger Workflow</a></li>\n</ol>\n<h2>Example event sources definition</h2>\n<pre><code class=\"language-yaml\">apiVersion: v1\nkind: ConfigMap\nmetadata:\n name: kafka-gateway-configmap\ndata:\n foo: |-\n   url: kafka.argo-events:9092 # kafka service\n   topic: foo # topic name\n   partition: \"0\" # topic partition\n bar: |-\n   url: kafka.argo-events:9092\n   topic: bar\n   partition: \"1\"\n</code></pre>\n<p>Create gateway event sources</p>\n<pre><code class=\"language-yaml\">kubectl -n argo-events create -f  https://github.com/argoproj/argo-events/blob/master/examples/gateways/kafka-gateway-configmap.yaml\n</code></pre>\n<h2>Install gateway</h2>\n<ol>\n<li>\n<p><strong>Create gateway</strong></p>\n<pre><code class=\"language-yaml\">kubectl -n argo-events create -f https://github.com/argoproj/argo-events/blob/master/examples/gateways/kafka.yaml\n</code></pre>\n</li>\n<li>\n<p><strong>Check the status of the gateway</strong></p>\n<pre><code class=\"language-yaml\">kubectl -n argo-events describe gateway kafka-gateway\n</code></pre>\n<p>Make sure the gateway is in active state and all the event sources are in running state.</p>\n</li>\n</ol>\n<h2>Install Sensor</h2>\n<pre><code class=\"language-yaml\">kubectl -n argo-events create -f https://github.com/argoproj/argo-events/blob/master/examples/sensors/kafka.yaml\n</code></pre>\n<h2>Trigger Workflow</h2>\n<p>Send a message to topic <code>foo</code> on partition <code>0</code>. You might find this useful <a href=\"https://kafka.apache.org/quickstart#quickstart_send\">https://kafka.apache.org/quickstart#quickstart_send</a> </p>","docPath":"argo-events/gateways/core/stream/kafka/install","proj":"argo-events"}}

